"use client";

import { useEffect, useState } from "react";
import { useSearchParams } from "next/navigation";
import ChaptersDrawer from "../../components/ChaptersDrawer";

export default function ChaptersPage({ params } : { 
  params: {id: string}
}) {
  return (
    <div className="flex">
      <ChaptersDrawer activeChapter={params.id} />
      <div className="flex-1 p-8 text-white">
        <h1 className="text-3xl font-bold">Chapter: {params.id}</h1>
        <p>Content of the selected chapter will go here...</p>
      </div>
    </div>
  );
};

