"use client";

import React from "react";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import Typography from "@mui/material/Typography";

const drawerWidth = 240;

interface ChaptersDrawerProps {
  activeChapter: string | null;
}

const ChaptersDrawer: React.FC<ChaptersDrawerProps> = ({ activeChapter }) => {
  const chapters = [
    "Celula si Organitele Celulare",
    "Respiratia",
    "Tesuturile",
    "Maduva Spinarii",
    "Sistemul Osos",
    "Sistemul Muscular",
  ];

  return (
    <Drawer
      variant="permanent"
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        "& .MuiDrawer-paper": { width: drawerWidth, boxSizing: "border-box" },
      }}
    >
      <Typography variant="h6" sx={{ p: 2 }}>
        Chapters
      </Typography>
      <List>
        {chapters.map((chapter, index) => (
          <ListItem key={index} disablePadding>
            <ListItemButton
              sx={{
                backgroundColor: activeChapter === chapter ? "#1976D2" : "transparent",
                color: activeChapter === chapter ? "#fff" : "#000",
                "&:hover": { backgroundColor: "#1565C0", color: "#fff" },
              }}
            >
              <ListItemText primary={chapter} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Drawer>
  );
};

export default ChaptersDrawer;
